@import url('https://fonts.googleapis.com/css2?family=PT+Sans&display=swap');

* {
    font-family: 'PT Sans', sans-serif;
}

#password-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-width: 90vw;
}

.form-group {
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.password-length {
    width: 7ch;
}

.center-horizontal {
    justify-content: center;
}

.button-styles {
    border-radius: 10px;
    padding: 10px;
    border: 0;
}

#submit-button {
    background-color: #00C853;
    transition: 1s all;
}

#submit-button:hover {
    background-color: #005e27;
    cursor: pointer;
    color: white;
}

#reset-button {
    background-color: #D50000;
    transition: 1s all;
    color: white;
}

#reset-button:hover {
    background-color: #8f0000;
    cursor: pointer;
}


.loader {
    border: 5px solid #f3f3f3;
    border-top: 5px solid #3498db;
    border-radius: 50%;
    width: 10px;
    height: 10px;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

#result {
    max-width: 88vw;
    word-wrap: break-word;
    display: flex;
    flex-direction: row;
}

#password {
    max-width: 80vw;
    text-align: justify;
}

#copy {
    align-self: center;
    cursor: pointer;
    font-size: 18px;
    text-decoration: none;
    color: black;
}

.tooltip {
    position: relative;
    display: inline-block;
}

.tooltip .tooltiptext {
    visibility: hidden;
    width: 140px;
    background-color: #555;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px;
    position: absolute;
    z-index: 1;
    bottom: 150%;
    left: 50%;
    margin-left: -75px;
    opacity: 0;
    transition: opacity 0.3s;
}

.tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
}